@media screen and (max-width: $sLaptop) {
	.about {
		.wrap {
			.subContents {
				.subSection {
					.inner {
						.sectionCont {
							#location {
								width: 90%;
								margin-left: 10%;
								.mapBtn {
									.mapBtnLists {
										.mapBtnList {
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
//$Ltablet: 992px;
@media screen and (max-width: $Ltablet) {
	#organization {
		> .inner {
			.aboutSectionCont {
				.organizationWarp {
					.organizationList {
						width: 50%;

						&:nth-last-child(-n + 4) {
							border-top: none;
						}
						&:nth-of-type(1),
						&:nth-of-type(4) {
							border-left: 1px solid $borderColor;
						}
						&:nth-of-type(3),
						&:nth-of-type(6) {
							border-left: 1px solid $borderColor;
						}
						&:nth-of-type(odd) {
							border-left: none;
						}
						&:nth-of-type(even) {
							border-right: none;
						}
					}
				}
			}
		}
	}
	#location {
		#map {
		}
		.mapTxt {
			width: 60%;
			margin-top: 20px;
			p {
				@include sectionText;
			}
		}
		.mapBtn {
			display: flex;
			align-items: flex-start;
			margin-top: 30px;
			.mapBtnTitle {
			}
			.mapBtnLists {
				width: 85%;
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				.mapBtnList {
					&:nth-of-type(3) {
						width: 100%;
						margin-right: 0;
					}
				}
			}
		}
	}
}
//$tablet : 778px;
@media screen and (max-width: $tablet) {
	#organization {
		> .inner {
			.aboutSectionCont {
				.organizationWarp {
					.organizationList {
						height: 400px;
						&.intro {
							.organizationBox {
								.organizationTitle {
									@include sectionTitle($fz: 30px, $fontWeight: bold);
									margin-bottom: 30px;
								}
								.organizationSub {
									@include sectionSubTitle($subFz: 20px, $fontWeight: 400);
								}
							}
						}
					}
				}
			}
		}
	}
	#location {
		width: 100%;
		margin-left: 0;
		#map {
			width: 100%;
			height: 300px;
		}
		.mapTxt {
			width: 70%;
			margin-top: 20px;
			p {
				@include sectionText;
			}
		}
		.mapBtn {
			flex-direction: column;
			.mapBtnTitle {
				width: 100%;
				margin-bottom: 30px;
			}
			.mapBtnLists {
				width: 100%;
				.mapBtnList {
					width: 100%;
					margin-right: 0;
				}
			}
		}
	}
}
//$mobile : 576px;
@media screen and (max-width: $mobile) {
	#organization {
		> .inner {
			.aboutSectionCont {
				.organizationWarp {
					.organizationList {
						width: 100%;
						height: 500px;
						border-left: none;
						border-right: none;
						padding: 20px;
						//margin-top: -1px;
						margin-left: 0;
						&::after {
							display: none;
						}
						&.intro {
							.organizationBox {
								.organizationTitle {
									@include sectionTitle($fz: 40px, $fontWeight: bold);
									margin-bottom: 30px;
								}
								.organizationSub {
									@include sectionSubTitle($subFz: 24px, $fontWeight: 400);
								}
								.organizationCaption {
									@include sectionText($captionFz: 16px);
								}
							}
						}
						&:nth-last-child(-n + 4) {
							border-top: 1px solid $borderColor;
						}
						&:nth-of-type(4) {
							border-left: none;
						}
						&:nth-of-type(6) {
							border-left: none;
							border-bottom: 1px solid $borderColor;
						}
					}
				}
			}
		}
	}
	#location {
		.mapTxt {
