.modal {
	width: 100%;
	height: 100vh;
	background: rgba(0, 0, 0, 0.9);
	padding: 7vh 5vw;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 40;
	.con {
		width: 100%;
		height: 100%;
		img,
		video,
		iframe {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border: none;
		}
	}
  .close {
    font: bold 14px/1 ;
    color: orange;
    position: absolute;
    top: 4vh;
    left: 50%;
    cursor: pointer;
  }
}
